















import Vue                 from 'vue'
import { Component, Prop } from 'vue-property-decorator';
import { Node }            from '@/app/core';

@Component<GraphNode>({
    name: 'GraphNode',
    components: {}
})
export default class GraphNode extends Vue {
    @Prop({required: true})
    point!: { x: number, y: number };
    @Prop({required: true})
    node!: Node;
    @Prop({required: true})
    nodeRadius!: number;
}
